import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { MessageService } from 'primeng/api';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DegreeRequestsLight, DegreeRequestData } from "../../core/interface/degree-requests";

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  providers: [MessageService]
})
export class CreateComponent implements OnInit, OnDestroy {

  createForm: FormGroup;
  formValid: boolean;
  submitted: boolean;

  protected readonly unsubscribe$ = new Subject<void>();

  constructor(
    private formBuilder: FormBuilder,
    private degreeRequestsService: DegreeRequestData,
    private messageService: MessageService) { }

  ngOnInit(): void {
    this.initForm();
  }

  initForm() {
    this.createForm = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      degreeName: ['', Validators.required],
      email: ['', [Validators.required, Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$")]],
    });
  }

  get f() { return this.createForm.controls; }

  onSubmit() {
    this.submitted = true;

    if (this.createForm.invalid) {
      return;
    }

    const firstName = this.createForm.get('firstName').value;
    const lastName = this.createForm.get('lastName').value;
    const degreeName = this.createForm.get('degreeName').value;
    const email = this.createForm.get('email').value;
    const newDegreeRequest: DegreeRequestsLight = {
      first_name: firstName,
      last_name: lastName,
      degree_name: degreeName,
      email: email,
    };

    this.degreeRequestsService.create(newDegreeRequest)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (response) => {
          this.createForm.reset();
          this.submitted = false;
          this.messageService.add({ severity: 'success', summary: 'Formulaire envoyé !', detail: 'Votre demande d\'attesation de réussite a bien été envoyé.' });
        },
        (error) => {
          this.messageService.add({ severity: 'error', summary: 'Une erreur est survenu', detail: 'Veuillez réessayer ou rafraîchir la page.' });
        });
  }

  onReset() {
    this.messageService.add({ severity: 'info', summary: 'Formulaire réinitialisé !', detail: '' });
    this.createForm.reset();
    this.submitted = false;
  }

  ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }
}
