import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DegreeRequestData, DegreeRequests, DegreeRequestsCustom, OneTimePassword } from 'src/app/core/interface/degree-requests';
import { MessageService } from 'primeng/api';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  providers: [MessageService]
})
export class ListComponent implements OnInit, OnDestroy {

  protected readonly unsubscribe$ = new Subject<void>();

  degreeRequestsTovalidate: DegreeRequests[];
  degreeRequestsValidated: DegreeRequests[];
  degreeRequestsRejected: DegreeRequests[];

  // Prime NG Table
  colsTovalidate: any[];
  colsValidated: any[];
  colsRejected: any[];
  selectedDegreeRequests: DegreeRequests[];
  activeIndex: number = 0;

  pageNumber: number = 1;
  pageSize: number = 10000;

  otp: OneTimePassword;
  otpForm: FormGroup;
  errorMessage: string = "";
  isOtpFormDisplay: boolean = false;
  formValid: boolean;
  submitted: boolean;
  submitting: boolean;
  otpId: string;
  isIdReceived: boolean = false;

  constructor(
    private degreeRequestsService: DegreeRequestData,
    private messageService: MessageService,
    private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.initForm();
    this.getList();

    this.colsTovalidate = [
      { field: 'first_name', header: 'Prénom' },
      { field: 'last_name', header: 'Nom' },
      { field: 'degree_name', header: 'Diplôme' },
      { field: 'email', header: 'Email' },
      { field: 'created', header: 'Demandé' }
    ];

    this.colsValidated = [
      { field: 'first_name', header: 'Prénom' },
      { field: 'last_name', header: 'Nom' },
      { field: 'degree_name', header: 'Diplôme' },
      { field: 'email', header: 'Email' },
      { field: 'created', header: 'Demandée' },
      { field: 'validated', header: 'Validée' },
      // { field: 'image', header: 'Image du diplôme' }
    ];

    this.colsRejected = [
      { field: 'first_name', header: 'Prénom' },
      { field: 'last_name', header: 'Nom' },
      { field: 'degree_name', header: 'Diplôme' },
      { field: 'email', header: 'Email' },
      { field: 'created', header: 'Demandée' },
      { field: 'rejected', header: 'Refusée' },
    ];
  }

  getList(): void {
    this.getListValidate();
    this.getListToValidate();
    this.getListRejected();
  }

  getListToValidate(): void {
    this.degreeRequestsService.list(this.pageNumber, this.pageSize, false, false)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (response) => {
          this.degreeRequestsTovalidate = response;
        },
        (error: any) => {
          console.error(error);
        }
      );
  }

  getListValidate(): void {
    this.degreeRequestsService.list(this.pageNumber, this.pageSize, true, '')
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (response) => {
          this.degreeRequestsValidated = response;
        },
        (error: any) => {
          console.error(error);
        }
      );
  }

  getListRejected(): void {
    this.degreeRequestsService.list(this.pageNumber, this.pageSize, '', true)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (response) => {
          this.degreeRequestsRejected = response;
        },
        (error: any) => {
          console.error(error);
        }
      );
  }

  initForm() {
    this.otpForm = this.formBuilder.group({
      password: ['', Validators.required],
    });
  }

  validate(dr: DegreeRequests) {
    this.selectedDegreeRequests = [dr];
    this.validateSelected();
  }

  reject(dr: DegreeRequests) {
    this.selectedDegreeRequests = [dr];
    this.rejectSelected();
  }

  validateSelected() {
    this.selectedDegreeRequests.forEach(element => {
      element.is_validated = true;
    });
    this.getIdOtp();
  }

  rejectSelected() {
    this.selectedDegreeRequests.forEach(element => {
      element.is_rejected = true;
    });
    this.getIdOtp();
  }

  getIdOtp() {
    this.degreeRequestsService.getOTP()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (response) => {
          this.otp = response['otp'];
          this.isOtpFormDisplay = true;
        },
        (error) => {
          console.error(error);
          this.messageService.add({ severity: 'error', summary: 'Une erreur est survenu', detail: 'Veuillez réessayer ou rafraîchir la page.' });
        }
      )
  }

  get f() { return this.otpForm.controls; }

  onSubmit() {
    this.submitted = true;
    this.submitting = true;
    this.errorMessage = "";

    if (this.otpForm.invalid) {
      this.submitting = false;
      return;
    }

    const password = this.otpForm.get('password').value;
    this.otp.password = password
    const degreeRequestCustom: DegreeRequestsCustom = {
      otp: this.otp,
      degree_requests: this.selectedDegreeRequests
    };

    this.degreeRequestsService.update(degreeRequestCustom)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (response) => {
          this.resetForm();
          this.submitting = false;
          if (degreeRequestCustom.degree_requests.length === 1) {
            this.messageService.add({ severity: 'success', summary: 'Mot de passe validé !', detail: 'La demande a bien été validée ou rejetée' });
          } else {
            this.messageService.add({ severity: 'success', summary: 'Mot de passe validé !', detail: 'Les demandes ont bien été validées ou rejetées' });
          }
        },
        (error) => {
          console.error(error);
          this.submitting = false;
          if (error.error.otp) {
            this.errorMessage = error.error.otp.password[0];
            if (error.error.otp.reset) {
              this.getIdOtp();
            }
          } else {
            this.messageService.add({ severity: 'error', summary: 'Une erreur est survenu', detail: 'Veuillez réessayer ou rafraîchir la page.' });
          }
        });
  }


  resetForm() {
    this.otpForm.reset();
    this.errorMessage = "";
    this.isOtpFormDisplay = false;
    this.submitted = false;
    this.otp = {
      id: '',
      password: ''
    };
    this.selectedDegreeRequests = [];
    this.getList();
  }

  onModalClose() {
    this.otpForm.reset();
    this.errorMessage = "";
    this.submitted = false;
    this.otp = {
      id: '',
      password: ''
    };
  }

  ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

}
