import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DegreeRequestData } from "../../core/interface/degree-requests";
import { environment } from "../../../environments/environment";

@Component({
  selector: 'app-verify',
  templateUrl: './verify.component.html',
  styleUrls: ['./verify.component.scss']
})
export class VerifyComponent implements OnInit {

  uploadForm: FormGroup;
  imageURL: string;
  submitted: boolean;
  isUploading: boolean;
  messageError: string;
  response: any;


  constructor(
    private formBuilder: FormBuilder,
    private degreeRequestsService: DegreeRequestData,
  ) { }

  ngOnInit(): void {
    this.uploadForm = this.formBuilder.group({
      image: ['', Validators.required],
    });
  }

  get f() { return this.uploadForm.controls; }

  onChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.uploadForm.get('image').setValue(file);
    }
  }

  onSubmit() {
    this.submitted = true;
    this.isUploading = true;
    this.messageError = "";
    this.response = undefined;
    this.imageURL = "";

    if (this.uploadForm.invalid) {
      this.isUploading = false;
      return;
    }

    const formData = new FormData();
    formData.append('file', this.uploadForm.get('image').value);

    this.degreeRequestsService.verify(formData).subscribe(
      (res) => {
        this.isUploading = false;
        this.submitted = false;

        this.response = res
        this.imageURL = `${environment.serverUrl}/media/${res.img_url}`;
      },
      (err) => {
        this.isUploading = false;
        this.messageError = err.error.file;
      }
    );
  }


}
